import React, { useState, useEffect } from "react";
import { getSettings, changeSettings } from "../services/AdminServices";
const AdminVip = ({ token }) => {
  const [settings, setSettings] = useState({ vip_message: "" });
  const [message, setMessage] = useState("");

  useEffect(() => {
    getSettings(token)
      .then(res => setSettings(res))
      .catch(err => console.log(err));
  }, [token]);

  const handleSubmit = e => {
    e.preventDefault();
    changeSettings(token, settings._id, settings)
      .then(res => setMessage("   "))
      .catch(err => setMessage("     "));
  };
  return (
    <main className="tap">
      <h2> VIP</h2>
      <div className="tap-box">
        <div className="tap-forms">
          <form onSubmit={handleSubmit}>
            <label htmlFor="vip"> </label>
            <textarea
              id="vip"
              value={settings.vip_message}
              onChange={e =>
                setSettings({ ...settings, vip_message: e.target.value })
              }
            />
            <br />
            <button type="submit"></button>
          </form>
          <br />
          {message}
          <hr />
        </div>
      </div>
    </main>
  );
};

export default AdminVip;
