import React, { useState, useEffect } from "react";
import {
  getAllCities,
  getAllCountries,
  editUser
} from "../services/AdminServices";

const AdminEditUser = ({ token, givenData, changeTap, users, setUsers }) => {
  const [countries, setCoutries] = useState([]);
  const [cities, setCities] = useState([]);
  const [message, setMessage] = useState("");
  const [userData, setUserData] = useState(givenData);
  const { name, username, ip } = userData;
  useEffect(() => {
    getAllCountries(token)
      .then(res => setCoutries(res))
      .catch(err => console.log(err));
    getAllCities(token)
      .then(res => setCities(res))
      .catch(err => console.log(err));
  }, [token]);
  const handleInputChange = e => {
    setUserData({ ...userData, [e.target.name]: e.target.value });
  };
  const handleSubmit = e => {
    e.preventDefault();
    if (userData.name === "") {
      return setMessage("  ");
    }
    editUser(token, userData._id, userData)
      .then(res => {
        let newUsers = [...users].map(user => {
          if (user._id === userData._id) {
            return res;
          } else {
            return user;
          }
        });
        setUsers(newUsers);
        setUserData({
          name: "",
          username: "",
          ip: "",
          age: 0,
          country: "",
          city: ""
        });
        changeTap("emails");
      })
      .catch(err =>
        setMessage(
          "           "
        )
      );
  };

  return (
    <main className="tap">
      <h2> </h2>
      <div className="tap-box">
        <div className="tap-forms create-user-form">
          <form onSubmit={handleSubmit}>
            <span className="message"> {message}</span>
            <div>
              <label htmlFor="name"></label>
              <input
                type="text"
                value={name}
                name="name"
                onChange={handleInputChange}
                id="name"
                required={true}
              />
            </div>
            <div>
              <label htmlFor="username"> </label>
              <input
                type="text"
                name="username"
                value={username}
                onChange={handleInputChange}
                id="username"
                required={true}
              />
            </div>
            <div>
              <label htmlFor="ip">IP</label>
              <input
                type="text"
                name="ip"
                value={ip}
                onChange={handleInputChange}
                id="ip"
                required={true}
              />
            </div>
            <div>
              <label htmlFor="country"></label>
              <select
                id="country"
                name="country"
                value={userData.country}
                onChange={handleInputChange}
              >
                <option value=""> </option>
                {countries.map(country => {
                  return (
                    <option key={country._id} value={country._id}>
                      {country.name}
                    </option>
                  );
                })}
              </select>
            </div>
            <div>
              <label htmlFor="city"></label>
              <select
                id="city"
                name="city"
                value={userData.city}
                onChange={handleInputChange}
              >
                <option value=""> </option>
                {cities
                  .filter(city => city.country_id === userData.country)
                  .map(city => {
                    return (
                      <option key={city._id} value={city._id}>
                        {city.name}
                      </option>
                    );
                  })}
              </select>
            </div>
            <div>
              <label htmlFor="age"></label>
              <input
                type="number"
                id="age"
                name="age"
                value={userData.age}
                onChange={handleInputChange}
              />
            </div>
            <div className="radio-box">
              <span></span>
              <input
                type="radio"
                name="sex"
                onChange={handleInputChange}
                value="0"
                defaultChecked={userData.sex === 0 ? true : false}
                id="male"
              />{" "}
              <label htmlFor="male" className="special-label">
                
              </label>
              <input
                type="radio"
                name="sex"
                onChange={handleInputChange}
                value="1"
                defaultChecked={userData.sex === 1 ? true : false}
                id="female"
              />{" "}
              <label htmlFor="female" className="special-label">
                
              </label>
            </div>
            <div className="radio-box">
              <span></span>
              <input
                type="radio"
                name="status"
                onChange={handleInputChange}
                value="0"
                id="normal"
                defaultChecked={userData.status === 0 ? true : false}
              />{" "}
              <label htmlFor="normal" className="special-label">
                
              </label>
              <input
                type="radio"
                name="status"
                onChange={handleInputChange}
                value="1"
                defaultChecked={userData.status === 1 ? true : false}
                id="vip"
              />{" "}
              <label htmlFor="vip" className="special-label">
                VIP
              </label>
            </div>
            <div className="radio-box">
              <span></span>
              <input
                type="radio"
                name="block"
                onChange={handleInputChange}
                defaultChecked={userData.block === 0 ? true : false}
                value="0"
                id="good"
              />{" "}
              <label htmlFor="good" className="special-label">
                
              </label>
              <input
                type="radio"
                name="block"
                value="1"
                onChange={handleInputChange}
                defaultChecked={userData.block === 1 ? true : false}
                id="blocked"
              />{" "}
              <label htmlFor="blocked" className="special-label">
                
              </label>
            </div>
            <button type="submit">  </button>
          </form>
        </div>
      </div>
    </main>
  );
};

export default AdminEditUser;
