import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getAllUsersForFilter } from "../services/UserServices";
// get all unique values
const getUnique = (persons, value) => {
  return [...new Set(persons.map(person => person[value]))];
};

function PersonsFilter({ setPage }) {
  const [search, setSearch] = useState({
    username: "",
    age: "",
    country: "",
    city: ""
  });

  const [persons, setPersons] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    getAllUsersForFilter()
      .then(res => {
        setPersons(res);
        setLoading(false);
      })
      .catch(err => console.log(err));
  }, []);
  const { username, age, country, city } = search;
  const handleChange = e => {
    setSearch({ ...search, [e.target.name]: e.target.value });
  };

  // get unique countries
  let countries = getUnique(persons, "country_name");

  // map to jsx
  countries = countries.map((item, index) => {
    return (
      <option value={item} key={index}>
        {item}
      </option>
    );
  });

  // get ages
  let ages = getUnique(persons, "age");

  // map to jsx
  ages = ages.map((item, index) => {
    return (
      <option key={index} value={item}>
        {item}
      </option>
    );
  });
  let query = `?${username ? `name=${username}&` : ""}${
    age ? `age=${age}&` : ""
  }${country ? `country=${country}&` : ""}${city ? `city=${city}&` : ""}`;
  if (query.endsWith("&")) query = query.replace(/&$/, "");
  // get unique cities
  let cities = getUnique(persons, "city_name");

  // map to jsx
  cities = cities.map((item, index) => {
    return (
      <option value={item} key={index}>
        {item}
      </option>
    );
  });
  if (loading) {
    return (
      <div className="special-loader-container">
        <div className="special-loader"></div>
      </div>
    );
  }
  return (
    <>
      <div className="persons-filter">
        <div className="search-form">
          <form className="filter-form">
            {/* name */}
            <div>
              <input
                name="username"
                id="username"
                value={username}
                placeholder=""
                className="form-control"
                onChange={handleChange}
              />
            </div>
            {/* end name */}
            {/* ages */}
            <div>
              <select
                name="age"
                id="age"
                value={age}
                className="form-control"
                onChange={handleChange}
              >
                <option value=""></option>
                {ages}
              </select>
            </div>
            {/* end ages */}
            {/* select Country */}
            <div>
              <select
                name="country"
                id="country"
                defaultValue={country}
                className="form-control"
                onChange={handleChange}
              >
                <option value=""></option>
                {countries}
              </select>
            </div>
            {/* end select country */}
            {/* select city */}
            <div>
              <select
                name="city"
                id="city"
                value={city}
                className="form-control"
                onChange={handleChange}
              >
                <option value=""></option>
                {cities}
              </select>
            </div>
            {/* end select city */}
          </form>
        </div>
        <Link
          to={{
            pathname: "/",
            search: query
          }}
          onClick={() => setPage(1)}
        >
          
        </Link>
      </div>
    </>
  );
}

export default PersonsFilter;
