import React, { useState, useEffect, useContext } from "react";
import ThemeContext from "../ThemeContext";
import {
  getVipUsers,
  getNormalUsers,
  getSettings,
  getSearchUsers
} from "../services/UserServices";
import { Link, useLocation } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import "../styles/pages/home.css";

import HeadingImage from "../assets/images/home/logotype.svg";
import PremiumCard from "../components/PremiumCard";
import PersonsFilter from "../components/PersonsFilter";
import LatestGhost from "../components/LatestGhost";

import ProfileImage from "../assets/images/home/profile-image.svg";
import GhostIcon from "../assets/images/home/ghost-icon.svg";
import AddIcon from "../assets/images/home/icons/add.svg";
import Loader from "../components/Loader";

const getUrlParameter = name => {
  name = name.replace(/[[]/, "\\[").replace(/[\]]/, "\\]");
  let regex = new RegExp("[\\?&]" + name + "=([^&#]*)");
  let results = regex.exec(window.location.search);
  return results === null
    ? ""
    : decodeURIComponent(results[1].replace(/\+/g, " "));
};
function Home() {
  const [normalUsers, setNormalUsers] = useState([]);
  const [vipUsers, setVipUsers] = useState([]);
  const [settings, setSettings] = useState({});
  const [loading, setLoading] = useState(true);
  const [ghostsLoading, setGhostsLoading] = useState(true);
  const [nSearch, setnSearch] = useState(0);
  const [page, setPage] = useState(1);
  const location = useLocation();
  const [theme] = useContext(ThemeContext);
  useEffect(() => {
    getSettings()
      .then(result => {
        setSettings(result);
        document.title = result.site_name;
        getVipUsers(1, result.vip_per_page)
          .then(res => setVipUsers(res))
          .catch(err => console.log(err));
      })
      .catch(err => console.log(err))
      .finally(() => setLoading(false));
  }, []);

  useEffect(() => {
    setGhostsLoading(true);
    if (location.search === "" || location.search === "?") {
      getNormalUsers(page, settings.normal_per_page)
        .then(res => setNormalUsers(res))
        .catch(err => console.log(err))
        .finally(() => setGhostsLoading(false));
    } else {
      if (location.search.match("city")) {
        let cityName = getUrlParameter("city");
        document.title = `${settings.site_name} ${cityName}`;
      } else if (location.search.match("country")) {
        let countryName = getUrlParameter("country");
        document.title = `${settings.site_name} ${countryName}`;
      }
      getSearchUsers(location.search, page, settings.normal_per_page)
        .then(res => {
          setNormalUsers(res.users);
          setnSearch(res.nFound);
        })
        .catch(err => console.log(err))
        .finally(() => setGhostsLoading(false));
    }
  }, [location.search, settings, page]);
  const paginator = () => {
    let l =
      location.search !== "" && location.search !== "?"
        ? nSearch
        : settings.nUsers;

    let nPages = Math.ceil(l / settings.normal_per_page);
    if (nPages <= 1) return null;
    let buttons = [];
    for (let i = 1; i <= nPages; i++) buttons.push(i);
    return (
      <div className="paginator">
        {buttons.map((button, index) => {
          return (
            <button type="button" key={index} onClick={() => setPage(button)}>
              {button}
            </button>
          );
        })}
      </div>
    );
  };
  if (loading) return <Loader />;

  return (
    <>
      <div className="home-page text-center">
        {/* Start Header */}
        <header>
          <Container>
            {/* Start Heading */}
            <div className="heading-page">
              <img src={HeadingImage} alt="heading" />
              <h2 className={theme.themeName}></h2>
            </div>
            {/* End Heading */}

            {/* Start Nav */}
            <nav className="d-none d-md-block">
              <Row>
                <Col md={4}>
                  <Link to="/" className="active">
                    {" "}
                  </Link>
                </Col>
                <Col md={4}>
                  <Link to="/add-account">  </Link>
                </Col>
                <Col md={4}>
                  <Link to="/subscription"></Link>
                </Col>
              </Row>
            </nav>
            {/* End Nav */}
          </Container>
        </header>
        {/* End Header */}

        {/* Start Body */}

        {/* Start Premium Ghosts */}
        <section className="premium-ghosts">
          <Container>
            <div className="main-heading">
              <h4> </h4>
            </div>

            <Row>
              {vipUsers.map(user => (
                <PremiumCard
                  key={user._id}
                  nameLink={""}
                  Image={ProfileImage}
                  ImageIcon={AddIcon}
                  name={user.name}
                  username={user.username}
                />
              ))}

              <Col xs={6} md={4} lg={3}>
                <div className="premium-card text-center">
                  <div className="item last">
                    <div className={`image-container ${theme.themeName}`}>
                      <img src={GhostIcon} alt="Profile" />
                    </div>
                    <h3> </h3>
                    <Link to="/subscription">
                      <span> </span>
                    </Link>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
        {/* End Premium Ghosts */}

        {/* Start Search */}
        <section className="search">
          <Container>
            <div className="main-heading">
              <h4>  </h4>
            </div>
            <PersonsFilter persons={normalUsers} setPage={setPage} />
          </Container>
        </section>
        {/* End Search */}

        {/* Latest Ghosts */}
        {ghostsLoading ? (
          <div className="special-loader-container">
            <div className="special-loader"></div>
          </div>
        ) : (
          <>
            <section className={`latest-ghosts ${theme.themeName}`}>
              <Container>
                <div className="main-heading">
                  <h4> </h4>
                </div>
                <Row>
                  <LatestGhost persons={normalUsers} link={settings.link} />
                </Row>
              </Container>
            </section>
            <section className="pages-controls d-md-none">
              <Container>
                <Link to="/add-account" className="active">
                   {" "}
                </Link>
                <Link to="/" className="hover">
                  {" "}
                </Link>
                <Link to="/subscription" className="premium">
                  
                </Link>
              </Container>
            </section>
            {paginator()}
          </>
        )}
        {/* End Ghosts */}

        {/* End Body */}
      </div>
    </>
  );
}

export default Home;
