import React, { useState, useEffect } from "react";
import { getSettings, changeSettings } from "../services/AdminServices";
const AdminSettings = ({ token }) => {
  const [settings, setSettings] = useState({
    site_name: "",
    site_description: "",
    vip_per_page: 0,
    normal_per_page: 0
  });
  const [message, setMessage] = useState("");
  useEffect(() => {
    getSettings(token)
      .then(res => setSettings(res))
      .catch(err => console.log(err));
  }, [token]);
  const handleSubmit = e => {
    e.preventDefault();
    changeSettings(token, settings._id, settings)
      .then(res => {
        setMessage("   ");
      })
      .catch(err => setMessage("    "));
  };
  return (
    <main className="tap">
      <h2> </h2>
      <div className="tap-box">
        <div className="tap-forms">
          <form onSubmit={handleSubmit}>
            <label htmlFor="settings"></label>
            <label htmlFor="name"> </label>
            <input
              type="text"
              id="name"
              value={settings.site_name}
              onChange={e =>
                setSettings({ ...settings, site_name: e.target.value })
              }
            />
            <label htmlFor="description"> </label>
            <textarea
              id="description"
              value={settings.site_description}
              onChange={e =>
                setSettings({ ...settings, site_description: e.target.value })
              }
            />
            <label htmlFor="vipPpage">
                VIP     
            </label>
            <input
              type="number"
              id="vipPpage"
              value={settings.vip_per_page}
              onChange={e =>
                setSettings({ ...settings, vip_per_page: e.target.value })
              }
            />
            <label htmlFor="normalPpage">
              {" "}
                    
            </label>
            <input
              type="number"
              id="normalPpage"
              value={settings.normal_per_page}
              onChange={e =>
                setSettings({ ...settings, normal_per_page: e.target.value })
              }
            />
            <br />
            <br />
            <button type="submit"></button>
          </form>
          {message}
          <hr />
        </div>
      </div>
    </main>
  );
};

export default AdminSettings;
