import React, { useState, useEffect } from "react";
import Loader from "../components/Loader";
import { logged, getAllUsers } from "../services/AdminServices";
import { useHistory } from "react-router-dom";
import AdminNav from "../components/AdminNav";
import AdminMain from "../components/AdminMain";
import AdminEmails from "../components/AdminEmails";
import AdminMessages from "../components/AdminMessages";
import AdminCountries from "../components/AdminCountries";
import AdminCities from "../components/AdminCities";
import AdminIPs from "../components/AdminIPs";
import AdminCreateUser from "../components/AdminCreateUser";
import AdminEditUser from "../components/AdminEditUser";
import AdminBlogs from "../components/AdminBlogs";
import AdminTags from "../components/AdminTags";
import "../styles/pages/admin.css";
import AdminSettings from "../components/AdminSettings";
import AdminVip from "../components/AdminVip";

const Admin = () => {
  const [loading, setLoading] = useState(true);
  const [tap, setTap] = useState("main");
  const [users, setUsers] = useState([]);
  const [usersToShow, setUsersToShow] = useState("all");
  const [editUserData, setEditUserData] = useState({});
  const history = useHistory();
  const [loggedAdmin] = useState(
    JSON.parse(window.localStorage.getItem("admin"))
  );

  useEffect(() => {
    if (loggedAdmin) {
      logged(loggedAdmin.token)
        .then(res => {
          if (!res.username) {
            history.replace("/admin-login");
          }
        })
        .catch(err => {
          history.replace("/admin-login");
        });
    } else {
      history.replace("/admin-login");
    }
  }, [history, loggedAdmin]);
  useEffect(() => {
    if (loggedAdmin) {
      getAllUsers(loggedAdmin.token)
        .then(res => {
          setLoading(false);
          setUsers(res);
        })
        .catch(err => console.log(err));
    } else {
      history.replace("/admin-login");
    }
  }, [history, loggedAdmin]);
  const usersToPass =
    usersToShow === "block"
      ? users.filter(user => user.block === 1)
      : usersToShow === "normal"
      ? users.filter(user => user.status === 0)
      : usersToShow === "vip"
      ? users.filter(user => user.status === 1)
      : users;
  if (loading) return <Loader />;

  const TapToRender = () => {
    if (!loggedAdmin) return null;
    switch (tap) {
      case "main":
        return <AdminMain token={loggedAdmin.token} users={users} />;
      case "emails":
        return (
          <AdminEmails
            token={loggedAdmin.token}
            setEditUserData={setEditUserData}
            changeTap={setTap}
            users={usersToPass}
            setUsers={setUsers}
          />
        );
      case "create-user":
        return (
          <AdminCreateUser
            token={loggedAdmin.token}
            users={users}
            setUsers={setUsers}
            changeTap={setTap}
          />
        );
      case "edit-user":
        return (
          <AdminEditUser
            token={loggedAdmin.token}
            givenData={editUserData}
            changeTap={setTap}
            users={users}
            setUsers={setUsers}
          />
        );
      case "messages":
        return <AdminMessages token={loggedAdmin.token} />;
      case "countries":
        return <AdminCountries token={loggedAdmin.token} />;
      case "cities":
        return <AdminCities token={loggedAdmin.token} />;
      case "ips":
        return <AdminIPs token={loggedAdmin.token} />;
      case "blogs":
        return <AdminBlogs token={loggedAdmin.token} />;
      case "tags":
        return <AdminTags token={loggedAdmin.token} />;
      case "settings":
        return <AdminSettings token={loggedAdmin.token} />;
      case "vip":
        return <AdminVip token={loggedAdmin.token} />;
      default:
        return <AdminMain token={loggedAdmin.token} />;
    }
  };
  return (
    <div className="admin">
      <AdminNav changeTap={setTap} setUsersToShow={setUsersToShow} />
      <div className="taps-header"></div>
      {TapToRender()}
    </div>
  );
};

export default Admin;
