import React from "react";
import { Link } from "react-router-dom";

import { Row, Col } from "react-bootstrap";

import Country from "../assets/images/home/icons/country.svg";
import City from "../assets/images/home/icons/city.svg";
import Cal from "../assets/images/home/icons/cal.svg";
import Age from "../assets/images/home/icons/age.svg";
import Sex from "../assets/images/home/icons/sex.svg";
import Clock from "../assets/images/home/icons/clock.svg";

function LatestGhost({ persons, link }) {
  if (persons.length === 0) {
    return (
      <div className="empty-search">
        <h3>  </h3>
      </div>
    );
  }

  return (
    <>
      {persons.map(person => {
        return (
          <Col lg={4} key={person._id}>
            <div className="ghost-item">
              <div className="heading-item">
                <h2>{person.name}</h2>
              </div>
              <div className="content-item">
                <Row>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={Country} alt="icon" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={City} alt="icon" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={Age} alt="icon" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.country_name}</h5>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.city_name}</h5>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.age}</h5>
                  </Col>
                </Row>
                <Row>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={Sex} alt="icon" width="20px" height="15px" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={Cal} alt="icon" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h4>
                      {" "}
                      <img src={Clock} alt="icon" width="20px" height="15px" />
                      &nbsp; 
                    </h4>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.sex === 0 ? "" : ""}</h5>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.date}</h5>
                  </Col>
                  <Col xs={4}>
                    <h5>{person.time}</h5>
                  </Col>
                </Row>
                <Row className="d-flex justify-content-center">
                  <Col xs={12}>
                    <a className="button-bg" href="/#">
                      
                    </a>
                  </Col>
                  <Col xs={4}>
                    <Link to="/report" className="button-sm text-danger">
                      
                    </Link>
                  </Col>
                  <Col xs={4}>
                    <a
                      className="button-sm"
                      href={`https://www.snapchat.com/add/${person.username}`}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      
                    </a>
                  </Col>
                </Row>
              </div>
              <div className="footer-item">
                <ul className="list-unstyled">
                  <li>
                    <a href={link} rel="noopener noreferrer" target="_blank">
                      {person.tags.join(" - ")}
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </Col>
        );
      })}
    </>
  );
}

export default LatestGhost;
