import React, { useState } from "react";
import { deleteUser } from "../services/AdminServices";
const AdminEmails = ({
  token,
  setEditUserData,
  changeTap,
  users,
  setUsers
}) => {
  const [search, setSearch] = useState("");

  const handleDelete = id => {
    deleteUser(token, id)
      .then(res => {
        setUsers(users.filter(user => user._id !== id));
      })
      .catch(err => console.log(err));
  };

  const usersToRender = users
    .filter(user => {
      let regex = new RegExp(search, "i");
      return user.name.match(regex);
    })
    .map((user, i) => {
      let {
        name,
        username,
        ip,
        country_name,
        city_name,
        age,
        sex,
        block,
        status
      } = user;
      block = block === 1 ? "" : "";
      status = status === 1 ? "vip" : "";
      sex = sex === 1 ? "" : "";
      return (
        <tr key={i}>
          <td>{name}</td>
          <td>{username}</td>
          <td>{ip}</td>
          <td>{country_name}</td>
          <td>{city_name}</td>
          <td>{age}</td>
          <td>
            <span className={sex === "" ? "tag tag-male" : "tag tag-female"}>
              {sex}
            </span>
          </td>
          <td>
            <span
              className={status === "vip" ? "tag tag-vip" : "tag tag-normal"}
            >
              {status}
            </span>
          </td>
          <td>
            <span
              className={block === "" ? "tag tag-blocked" : "tag tag-good"}
            >
              {block}
            </span>
          </td>
          <td>
            <button
              type="button"
              className="edit"
              onClick={() => {
                setEditUserData(user);
                changeTap("edit-user");
              }}
            >
              
            </button>
            <button
              type="button"
              onClick={() => handleDelete(user._id)}
              className="delete"
            >
              
            </button>
          </td>
        </tr>
      );
    });
  return (
    <main className="tap">
      <h2></h2>
      <div className="tap-box">
        <div className="tap-forms">
          <form>
            <label htmlFor="search"></label>
            <input
              type="text"
              value={search}
              onChange={e => setSearch(e.target.value)}
            />
            <span
              className="span-link"
              onClick={() => changeTap("create-user")}
            >
              +   
            </span>
          </form>
          <hr />
        </div>
        <div className="tap-table">
          <table>
            <thead>
              <tr>
                <td></td>
                <td> </td>
                <td>IP</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tr>
            </thead>
            <tbody>{usersToRender}</tbody>
          </table>
        </div>
      </div>
    </main>
  );
};

export default AdminEmails;
