import React, { useState, useEffect } from "react";
import { useParams, Link } from "react-router-dom";
import { getSingleBlog } from "../services/UserServices";
import Loader from "../components/Loader";
import { Container } from "react-bootstrap";
import Arrow from "../assets/images/blog/icons/arrow.svg";
import "../styles/pages/singleBlog.css";
const SingleBlog = () => {
  const params = useParams();
  const title = params.title;
  const [blog, setBlog] = useState({
    title: "",
    text: "",
    type: "",
    created_at: "date time"
  });
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    getSingleBlog(title)
      .then(res => {
        if (!res) {
          setError(true);
        } else {
          setBlog(res);
        }
      })
      .catch(err => console.log(err));
    setLoading(false);
  }, [title]);
  const blogSection = () => {
    return (
      <section className="single-blog">
        <span>: {blog.created_at.split(" ")[0]}</span>
        <br />
        <span>
          :{" "}
          {blog.type === "news"
            ? ""
            : blog.type === "favorite"
            ? ""
            : ""}
        </span>
        <h2>{blog.title}</h2>
        <p>{blog.text}</p>
      </section>
    );
  };
  const NotFoundSection = () => {
    return (
      <div>
        <h2>  </h2>
      </div>
    );
  };
  if (loading) return <Loader />;
  return (
    <div className="blog-page">
      {/* Start Header */}
      <header>
        <Container>
          {/* Start Heading */}
          <div className="heading-page">
            <Link to="/blog">
              <img src={Arrow} alt="Back" />
            </Link>
            <h2 className="text-align"></h2>
          </div>
          {/* End Heading */}
        </Container>
      </header>
      {/* End Header */}
      {error ? NotFoundSection() : blogSection()}
      <section className="pages-controls d-md-none">
        <Container>
          <Link to="/add-account" className="active">
             {" "}
          </Link>
          <Link to="/" className="hover">
            {" "}
          </Link>
          <Link to="/subscription" className="premium">
            
          </Link>
        </Container>
      </section>
    </div>
  );
};

export default SingleBlog;
