import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { getSettings } from "../services/UserServices";
import { Container, Row, Col } from "react-bootstrap";

import Arrow from "../assets/images/blog/icons/arrow.svg";

function Subscription() {
  const [settings, setSettings] = useState({ vip_message: "" });
  useEffect(() => {
    getSettings()
      .then(res => setSettings(res))
      .catch(err => console.log(err));
  }, []);
  return (
    <div className="subscription">
      {/* Start Header */}
      <header>
        <Container>
          {/* Start Heading */}
          <div className="heading-page">
            <Link to="/add-account">
              <img src={Arrow} alt="Back" />
            </Link>
            <h2 className="text-align"></h2>
          </div>
          {/* End Heading */}
          {/* Start Nav */}
          <nav className="d-none d-md-block text-center">
            <Row>
              <Col md={4}>
                <Link to="/"> </Link>
              </Col>
              <Col md={4}>
                <Link to="/add-account">  </Link>
              </Col>
              <Col md={4}>
                <Link to="/subscription" className="active">
                  
                </Link>
              </Col>
            </Row>
          </nav>
          {/* End Nav */}
        </Container>
      </header>
      {/* End Header */}

      {/* Start Body */}
      <section className="subscription-body text-center">
        <Container>
          <div className="subscription-form">
            <h3>{settings.vip_message}</h3>
          </div>
        </Container>
      </section>
      {/* End Body */}

      <section className="pages-controls d-md-none">
        <Container>
          <Link to="/add-account" className="active">
             {" "}
          </Link>
          <Link to="/" className="hover">
            {" "}
          </Link>
          <Link to="/subscription" className="premium">
            
          </Link>
        </Container>
      </section>
    </div>
  );
}

export default Subscription;
