import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import { getAllBlogs } from "../services/UserServices";
// components
import NewsCard from "../components/NewsCard";
import Loader from "../components/Loader";
import Arrow from "../assets/images/blog/icons/arrow.svg";

function Blog() {
  const [blogs, setBlogs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [classification, setClassification] = useState("all");
  useEffect(() => {
    getAllBlogs()
      .then(res => {
        setBlogs(res);
        setLoading(false);
      })
      .catch(err => {
        console.log(err);
        setLoading(false);
      });
  }, []);
  const blogsToRender = () => {
    switch (classification) {
      case "all":
        return blogs;
      case "news":
        return blogs.filter(blog => blog.type === "news");
      case "articles":
        return blogs.filter(blog => blog.type === "articles");
      case "favorite":
        return blogs.filter(blog => blog.type === "favorite");
      default:
        return blogs;
    }
  };

  if (loading) return <Loader />;
  return (
    <div className="blog-page">
      {/* Start Header */}
      <header>
        <Container>
          {/* Start Heading */}
          <div className="heading-page">
            <a href="/#">
              <img src={Arrow} alt="Back" />
            </a>
            <h2 className="text-align"></h2>
          </div>
          {/* End Heading */}

          {/* Start Nav */}
          <nav>
            <Row>
              <Col xs={6} md={3}>
                <button
                  type="button"
                  className={classification === "all" ? "active" : ""}
                  onClick={() => setClassification("all")}
                >
                  
                </button>
              </Col>
              <Col xs={6} md={3}>
                <button
                  type="button"
                  className={classification === "news" ? "active" : ""}
                  onClick={() => setClassification("news")}
                >
                  
                </button>
              </Col>
              <Col xs={6} md={3}>
                <button
                  type="button"
                  className={classification === "articles" ? "active" : ""}
                  onClick={() => setClassification("articles")}
                >
                  
                </button>
              </Col>
              <Col xs={6} md={3}>
                <button
                  type="button"
                  className={classification === "favorite" ? "active" : ""}
                  onClick={() => setClassification("favorite")}
                >
                  
                </button>
              </Col>
            </Row>
          </nav>
          {/* End Nav */}
        </Container>
      </header>
      {/* End Header */}

      {/* Start Body */}

      {/* Start News cards */}
      <section className="news-cards">
        <Container>
          <Row>
            {blogsToRender().length > 0 ? (
              blogsToRender().map(blog => (
                <NewsCard key={blog._id} blog={blog} />
              ))
            ) : (
              <h2>  </h2>
            )}
          </Row>
        </Container>
      </section>
      {/* End News cards */}

      {/* Start Read more button */}
      <section className="read-more text-center">
        <Container>
          <a href="/#"> </a>
        </Container>
      </section>
      {/* End Read more button */}

      {/* End Body */}

      <section className="pages-controls d-md-none">
        <Container>
          <Link to="/add-account" className="active">
             {" "}
          </Link>
          <Link to="/" className="hover">
            {" "}
          </Link>
          <Link to="/subscription" className="premium">
            
          </Link>
        </Container>
      </section>
    </div>
  );
}

export default Blog;
