import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  getAllBlogs,
  addBlog,
  editBlog,
  deleteBlog
} from "../services/AdminServices";

const AdminBlogs = ({ token }) => {
  const [blogs, setBlogs] = useState([]);
  const [seacrh, setSearch] = useState("");
  const [newBlog, setNewBlog] = useState({ title: "", text: "", type: "news" });
  const [message, setMessage] = useState("");
  const [editBox, setEditBox] = useState(false);
  const [editBlogData, setEditBlogData] = useState({
    title: "",
    text: "",
    type: "news"
  });
  const [editMessage, setEditMessage] = useState("");

  useEffect(() => {
    getAllBlogs(token)
      .then(res => {
        setBlogs(res);
      })
      .catch(err => console.log(err));
  }, [token]);
  const handleAdd = e => {
    e.preventDefault();
    addBlog(token, newBlog)
      .then(res => {
        if (res.error) {
          setMessage(res.error);
        } else {
          let blogsUpdated = [...blogs];
          blogsUpdated.unshift(res);
          setBlogs(blogsUpdated);
          setMessage("   ");
          setNewBlog({ title: "", text: "", type: "news" });
        }
      })
      .catch(err => {
        setMessage("     ");
      });
  };
  const handleShowEdit = blogData => {
    setEditBox(true);
    setEditBlogData(blogData);
  };
  const handleEdit = e => {
    e.preventDefault();
    editBlog(token, editBlogData._id, editBlogData)
      .then(res => {
        if (res.error) {
          setMessage(res.error);
        } else {
          setBlogs(
            blogs.map(blog => {
              if (blog._id === editBlogData._id) return res;
              return blog;
            })
          );

          setEditMessage("   ");
        }
      })
      .catch(err => setMessage("     "));
  };
  const handleDelete = id => {
    deleteBlog(token, id)
      .then(res => {
        setBlogs(blogs.filter(blog => blog._id !== id));
      })
      .catch(err => console.log(err));
  };
  return (
    <main className="tap">
      <h2></h2>
      <div className="tap-box">
        <div className="tap-forms">
          <form onSubmit={e => e.preventDefault()}>
            <label htmlFor="search"></label>
            <input
              type="text"
              value={seacrh}
              id="search"
              onChange={e => setSearch(e.target.value)}
            />
          </form>
          <hr />
          <form onSubmit={handleAdd}>
            <label htmlFor="add"> </label>
            <label htmlFor="title"> </label>
            <input
              type="text"
              id="title"
              value={newBlog.title}
              className="title-box"
              onChange={e => setNewBlog({ ...newBlog, title: e.target.value })}
              required={true}
            />
            <label htmlFor="text"> </label>
            <textarea
              id="text"
              className="text-box"
              value={newBlog.text}
              onChange={e => setNewBlog({ ...newBlog, text: e.target.value })}
              required={true}
            ></textarea>
            <br />
            <label htmlFor="type">  </label>
            <select
              onChange={e => setNewBlog({ ...newBlog, type: e.target.value })}
              id="type"
            >
              <option value="news"></option>
              <option value="articles"></option>
              <option value="favorite"></option>
            </select>
            <br />
            <br />
            <button type="submit"> </button>
          </form>
          <br />
          {message}
          <hr />
          {editBox && (
            <>
              <form onSubmit={handleEdit}>
                <label htmlFor="edit">  </label>
                <label htmlFor="edit-title"> </label>
                <input
                  type="text"
                  id="edit-title"
                  value={editBlogData.title}
                  className="title-box"
                  onChange={e =>
                    setEditBlogData({ ...editBlogData, title: e.target.value })
                  }
                  required={true}
                />
                <label htmlFor="edit-text"> </label>
                <textarea
                  id="edit-text"
                  value={editBlogData.text}
                  className="text-box"
                  onChange={e =>
                    setEditBlogData({ ...editBlogData, text: e.target.value })
                  }
                  required={true}
                ></textarea>
                <br />
                <label htmlFor="edit-type">  </label>
                <select
                  onChange={e =>
                    setEditBlogData({ ...editBlogData, type: e.target.value })
                  }
                  defaultValue={editBlogData.type}
                  id="edit-type"
                >
                  <option value="news"></option>
                  <option value="articles"></option>
                  <option value="favorite"></option>
                </select>
                <br />
                <br />
                <button
                  type="button"
                  style={{ marginLeft: "1rem" }}
                  onClick={() => setEditBox(false)}
                >
                  {" "}
                   
                </button>
                <button type="submit"> </button>
              </form>
              <br />
              {editMessage}
              <hr />
            </>
          )}
        </div>
      </div>
      <div className="blogs-container">
        <h2></h2>
        {blogs
          .filter(blog => {
            let regex = new RegExp(seacrh, "ig");
            return blog.title.match(regex);
          })
          .map(blog => {
            return (
              <div className="blog" key={blog._id}>
                <div className="blog-header">
                  <div className="blog-header-right">
                    <h2>{blog.title}</h2>
                    <span>
                      {blog.type === "news"
                        ? ""
                        : blog.type === "favorite"
                        ? ""
                        : ""}
                    </span>
                    <span>{blog.created_at.split(" ")[0]}</span> <br />
                    <Link
                      to={{
                        pathname: `blog/${blog.title.replace(/\s/g, "_")}`
                      }}
                      target="_blank"
                    >
                           
                    </Link>
                  </div>
                  <div className="blog-header-left">
                    <button
                      type="button"
                      onClick={() => handleShowEdit(blog)}
                      className="edit"
                    >
                      
                    </button>
                    <button
                      type="button"
                      onClick={() => handleDelete(blog._id)}
                      className="delete"
                    >
                      
                    </button>
                  </div>
                </div>
                <hr />
                <div className="blog-text">{blog.text}</div>
              </div>
            );
          })}
      </div>
    </main>
  );
};

export default AdminBlogs;
