import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  getAllCities,
  getAllCountries,
  addPerson,
  getIP,
  getAllMessages
} from "../services/UserServices";
import { Container, Row, Col } from "react-bootstrap";
import Arrow from "../assets/images/blog/icons/arrow.svg";

function AddAccount() {
  const [countries, setCountries] = useState([]);
  const [cities, setCities] = useState([]);
  const [ip, setIP] = useState("");
  const [messages, setMessages] = useState([]);
  const [message, setMessage] = useState("");
  const [showForm, setShowForm] = useState(true);
  const [userData, setUserData] = useState({
    name: "",
    username: "",
    age: "",
    message: "",
    country: "",
    city: ""
  });
  const [nameRequired, setNameRequired] = useState("");
  const [usernameRequired, setUserNameRequired] = useState("");
  const [countryRequired, setCountryRequired] = useState("");
  const [cityRequired, setCityRequired] = useState("");
  const [buttonDisabled, setButtonDisables] = useState(false);
  useEffect(() => {
    getAllCountries()
      .then(res => setCountries(res))
      .catch(err => console.log(err));
    getAllCities()
      .then(res => setCities(res))
      .catch(err => console.log(err));
    getIP()
      .then(res => setIP(res))
      .catch(err => console.log(err));
    getAllMessages()
      .then(res => setMessages(res))
      .catch(err => console.log(err));
  }, []);
  const handleChange = e => {
    setUserData({ ...userData, [e.target.name]: e.target.value });
  };

  const handleSubmit = () => {
    setButtonDisables(true);
    setNameRequired("");
    setUserNameRequired("");
    setCountryRequired("");
    setCityRequired("");
    if (userData.name === "") {
      setButtonDisables(false);
      return setNameRequired("  ");
    }
    if (userData.username === "") {
      setButtonDisables(false);
      return setUserNameRequired("   ");
    }
    if (userData.country === "") {
      setButtonDisables(false);
      return setCountryRequired("  ");
    }
    if (userData.city === "") {
      setButtonDisables(false);
      return setCityRequired("  ");
    }

    addPerson({ ...userData, ip })
      .then(res => {
        setMessage("  ");
        setShowForm(false);
      })
      .catch(err => console.log(err));
  };

  return (
    <div className="add-account">
      {/* Start Header */}
      <header>
        <Container>
          {/* Start Heading */}
          <div className="heading-page">
            <Link to="/">
              <img src={Arrow} alt="Back" />
            </Link>
            <h2 className="text-align"> </h2>
          </div>
          {/* End Heading */}
          {/* Start Nav */}
          <nav className="d-none d-md-block text-center">
            <Row>
              <Col md={4}>
                <Link to="/"> </Link>
              </Col>
              <Col md={4}>
                <Link to="/add-account" className="active">
                   {" "}
                </Link>
              </Col>
              <Col md={4}>
                <Link to="/subscription"></Link>
              </Col>
            </Row>
          </nav>
          {/* End Nav */}
        </Container>
      </header>
      {/* End Header */}

      {/* Start Body */}
      <section className="add-body text-center">
        {message && <div className="success-box">{message}</div>}
        {showForm && (
          <>
            <form className="user-form" onSubmit={e => e.preventDefault()}>
              <label htmlFor="name"></label>
              <input
                type="text"
                id="name"
                name="name"
                value={userData.name}
                onChange={handleChange}
              />
              <span className="required-message">{nameRequired}</span>
              <label htmlFor="username" className="username-label">
                 
              </label>
              <input
                type="text"
                id="username"
                name="username"
                value={userData.username}
                onChange={handleChange}
              />
              <span className="required-message">{usernameRequired}</span>
              <label htmlFor="age"></label>
              <input
                type="number"
                id="age"
                name="age"
                value={userData.age}
                onChange={handleChange}
              />
              <label htmlFor="sex"></label>
              <select id="sex" name="sex" onChange={handleChange}>
                <option value=""></option>
                <option value="0"></option>
                <option value="1"></option>
              </select>
              <label htmlFor="country"></label>
              <select
                id="country"
                name="country"
                onChange={handleChange}
                required={true}
              >
                <option value=""></option>
                {countries.map(country => {
                  return (
                    <option key={country._id} value={country._id}>
                      {country.name}
                    </option>
                  );
                })}
              </select>
              <span className="required-message">{countryRequired}</span>
              <label htmlFor="city"></label>
              <select
                id="city"
                name="city"
                onChange={handleChange}
                required={true}
              >
                <option value=""></option>
                {cities
                  .filter(city => city.country_id === userData.country)
                  .map(city => {
                    return (
                      <option key={city._id} value={city._id}>
                        {city.name}
                      </option>
                    );
                  })}
              </select>
              <span className="required-message">{cityRequired}</span>
              <label htmlFor="message"></label>
              <select id="message" name="message" onChange={handleChange}>
                <option value=""></option>
                {messages.map(message => {
                  return (
                    <option key={message._id} value={message.text}>
                      {message.text}
                    </option>
                  );
                })}
              </select>
            </form>
            <button
              type="button"
              onClick={handleSubmit}
              className="special-button"
              disabled={buttonDisabled}
            >
               
            </button>
          </>
        )}
      </section>
      {/* End Body */}

      <section className="pages-controls d-md-none">
        <Container>
          <Link to="/add-account" className="active">
             {" "}
          </Link>
          <Link to="/" className="hover">
            {" "}
          </Link>
          <Link to="/subscription" className="premium">
            
          </Link>
        </Container>
      </section>
    </div>
  );
}

export default AddAccount;
